<template>
  <div class="paginate">
    <div class="btn">
      <button @click="prevPage" class="prev-btn">
        <i class="fa fa-chevron-left" aria-hidden="true"></i>
      </button>
      <button @click="nextPage" class="next-btn">
        <i class="fa fa-chevron-right" aria-hidden="true"></i>
      </button>
    </div>
  </div>
</template>

<script>
import { ref } from "vue";
export default {
  name: "PaginateBtn",
  props: ["docData", "token", "category"],
  setup(props, { emit }) {
    const host = process.env.VUE_APP_HOST
    const recodes = ref(0);
    const nextPage = () => {
      recodes.value = recodes.value + 10;
      const searchString = ref("");
      if (typeof props.token == "number") {
        searchString.value = `${host}paginate/?recodes=${
          recodes.value
        }&token=${Number(props.token)}&category=${props.category}`;
      } else {
        searchString.value = `${host}paginate/?recodes=${recodes.value}&token=${props.token}&category=${props.category}`;
      }

      fetch(searchString.value)
        .then((response) => response.json())
        .then((data) => {
          if (data.result) {
            emit("updateDoc", data.result);
            props.docData.value = data.result;
          }
          if (data.message) {
            recodes.value = recodes.value - 10;
            alert(data.message);
          }
        });
    };
    const prevPage = () => {
      recodes.value = recodes.value - 10;
      if (recodes.value < 0) {
        recodes.value = 0;
      }
      const searchString = ref("");
      if (typeof props.token == "number") {
        searchString.value = `${host}paginate/?recodes=${
          recodes.value
        }&token=${Number(props.token)}&category=${props.category}`;
      } else {
        searchString.value = `${host}paginate/?recodes=${recodes.value}&token=${props.token}&category=${props.category}`;
      }

      fetch(searchString.value)
        .then((response) => response.json())
        .then((data) => {
          if (data.result) {
            emit("updateDoc", data.result);
            props.docData.value = data.result;
          }
          if (data.Error) {
            recodes.value = 0;
          }
        });
    };

    return { nextPage, prevPage };
  },
};
</script>

<style lang="scss">
.paginate {
  padding: 20px;
  .btn {
    text-align: center;
    button {
      padding: 10px 40px;
      border: 0;
      margin: 10px;
      border-radius: 5px;
      background: dodgerblue;
      color: #fff;
      font-size: 1rem;
      transition: 0.5s ease;
      box-shadow: 5px 5px rgb(185, 184, 184);
      cursor: pointer;
      &:hover {
        box-shadow: -5px -5px rgb(219, 218, 218);
      }
    }
  }
}
</style>
