<template>
  <header>
    <nav class="nav">
      <div class="logo">
        <h1>{ <span>docbydev</span> }</h1>
      </div>
      <ul class="nav-ul">
        <router-link class="nav-link" to="/">
          <li class="nav-li">
            home
          </li>
        </router-link>
        <router-link class="nav-link" to="/publicdoc">
          <li class="nav-li">
            public docs
          </li>
        </router-link>
        <router-link v-if="!userToken" class="nav-link" to="/login">
          <li class="nav-li">
            login
          </li>
        </router-link>
        <router-link v-if="!userToken" class="nav-link" to="/signup">
          <li class="nav-li">
            signup
          </li>
        </router-link>
        <router-link v-if="userToken" class="nav-link" to="/auth/profile">
          <li class="nav-li">
            profile
          </li>
        </router-link>
        <li v-if="userToken" class="nav-li" @click="logOut">
          logout
        </li>
        <router-link v-if="userToken" class="nav-link" to="/auth/adddoc">
          <li class="nav-li">
            add doc
          </li>
        </router-link>
      </ul>
      <div class="menu-btn" @click="menuBtn">
        <span></span>
        <span></span>
        <span></span>
      </div>
    </nav>
  </header>
</template>

<script>
import { ref, watch } from "vue";
import { useRouter } from "vue-router";
import { useStore } from "vuex";
export default {
  name: "Nav",
  setup() {
    const store = useStore();
    const userToken = ref(store.getters.currentUserToken);
    const routeName = ref("/");
    const router = useRouter();
    const host = process.env.VUE_APP_HOST
    watch(router.currentRoute, () => {
      routeName.value = router.currentRoute._rawValue.name;
      store.commit("updateUserToken");
      userToken.value = store.getters.currentUserToken;
    });
    // Checking window width and updating ui to given width
    const mediaQuery = "(max-width: 470px)";
    const mediaQueryList = window.matchMedia(mediaQuery);
    mediaQueryList.addEventListener("change", (event) => {
      if (event.matches) {
        document.querySelector(".nav-ul").style.height = "0px";
      } else {
        document.querySelector(".nav-ul").style.height = "auto";
      }
    });
    const menuBtn = (e) => {
      const height = document.querySelector(".nav-ul").style.height;
      if (height == "0px") {
        document.querySelector(".nav-ul").style.height = "400px";
      } else {
        document.querySelector(".nav-ul").style.height = "0px";
      }
    };
    const logOut = () => {
      fetch(`${host}logout`, {
        method: "POST",
        body: JSON.stringify(userToken),
        headers: { "Content-type": "application/json; charset=UTF-8" },
      })
        .then((response) => response.json())
        .then((data) => {
          if (data.message) {
            alert(data.message);
            document.cookie = "token" + "=; Max-Age=0";
            store.commit("updateUserToken");
            userToken.value = store.getters.currentUserToken;
            router.push({ name: "Login" });
          }
        });
    };
    return { routeName, menuBtn, mediaQueryList, userToken, logOut };
  },
};
</script>

<style lang="scss">
header {
  position: fixed;
  z-index: 4;
  width: 100%;
  background: #fff;
  border-bottom: 1px solid #2222;
  .nav {
    padding: 10px 0px;
    display: flex;
    align-items: center;
    justify-content: space-around;
    flex-wrap: wrap;
    .logo {
      padding: 0px 20px;
      h1 {
        font-size: 1.5rem;
        text-transform: uppercase;
        color: dodgerblue;
        letter-spacing: 2px;
        font-weight: 900;
        padding: 5px 0px;
        span {
          color: #222;
        }
      }
    }
    .nav-ul {
      height: auto;
      transition: 0.5s ease;
      padding: 0px 20px;
      text-decoration: none;
      .nav-link {
        text-decoration: none;
        font-weight: 400;
        margin: 5px;
      }
      .nav-li {
        list-style: none;
        display: inline-block;
        padding: 10px 20px;
        text-decoration: none;
        text-transform: capitalize;
        font-size: 1rem;
        color: #222;
        font-weight: 400;
        border-radius: 3px;
        cursor: pointer;
        &:hover {
          background: #222;
          color: #fff;
        }
      }
    }
    .menu-btn {
      display: none;
    }
    .active {
      background: #222;
      padding: 10px 0px;
      border-radius: 5px;
      .nav-li {
        color: #fff;
        background: #222;
      }
    }
  }
}
@media only screen and(max-width:745px) {
  header {
    .nav {
      justify-content: center;
      .nav-ul {
        .nav-li {
          padding: 10px;
        }
      }
    }
  }
}
@media only screen and(max-width:470px) {
  header {
    .nav {
      display: grid;
      grid-template-columns: 1fr 0.3fr;
      grid-template-rows: auto;
      padding: 0px;
      .logo {
        grid-column: 1 / 2;
        grid-row: 1 /2;
        padding: 15px 10px;
        h1 {
          font-size: 1.5rem;
        }
      }
      .nav-ul {
        grid-column: 1 / 3;
        grid-row: 2 /3;
        height: 0px;
        padding: 0px;
        overflow: hidden;
        .nav-li {
          display: flex;
          justify-content: center;
          align-items: center;
          font-size: 1.3rem;
          width: 80%;
          margin: auto;
        }
      }
      .menu-btn {
        display: block;
        grid-column: 2 /3;
        grid-row: 1 /2;
        justify-self: right;
        padding: 0px 20px;
        span {
          display: block;
          background: #58391c;
          width: 20px;
          height: 3px;
          margin: 4px;
        }
      }
    }
  }
}
</style>
