<template>
  <div class="user-profile">
    <!-- User details sec   -->
    <section class="user-details">
      <div class="user">
        <div class="user-info">
          <span v-if="userProfile" class="profile-img"
            ><img :src="userProfile" alt=""
          /></span>
          <span v-else class="profile-img"
            ><img
              src="https://docbydev.herokuapp.com/assets/uploads/profileImages/image-1612783779868.jpeg "
              alt=""
          /></span>
          <span>
            <h1><b>Name</b> : {{ userName }}</h1>
            <p
              :class="{ expend: expendCount }"
              @click="readDetails"
              v-if="userBio"
            >
              <b>Bio</b> : {{ userBio }}
            </p>
            <p :class="{ expend: expendCount }" @click="readDetails" v-else>
              <b>Bio</b> : edit you bio
            </p>
            <p
              :class="{ expend: expendCount }"
              @click="readDetails"
              v-if="userSkills"
            >
              <b>Skill</b> {{ userSkills }}
            </p>
            <p :class="{ expend: expendCount }" @click="readDetails" v-else>
              <b>Skill</b> skills
            </p>
          </span>
        </div>
      </div>
    </section>
    <!-- User Docs  -->
    <section class="user-docs">
      <nav class="user-nav">
        <ul>
          <li @click="changeCategory" data-value="latestDoc">latest docs</li>
          <li @click="changeCategory" data-value="mostLikedDoc">
            most liked docs
          </li>
        </ul>
      </nav>
      <div class="user-doc">
        <p v-if="docData">{{ categoryText }}</p>
        <ul>
          <li
            class="doc-li"
            v-for="data in docData"
            :key="data.id"
            :data-id="data.id"
          >
            <router-link
              :to="{
                name: 'Doc',
                params: {
                  docData: data.docData.slice(1, -1),
                  Username: data.Username,
                  uploadDate: data.uploadDate.replace(/(.{25})..+/, '$1…'),
                  docTitle: data.docTitle,
                  docTags:data.docTags,
                  profileImage: data.ProfileImage,
                  docId: data.id,
                  userId: data.userId,
                },
              }"
              class="title"
            >
              <h1>
                {{ data.docTitle.replace(/(.{25})..+/, "$1…") }}
                <br />
                <span
                  v-for="(tag, index) in data.docTags.split(',')"
                  :key="index"
                  class="doc-tag"
                >
                  {{ tag }}
                </span>
              </h1>
            </router-link>
            <div class="doc-info">
              <span class="doc-auther"> Auther : {{ data.Username }} </span>
              <span class="post-date">
                Posted : {{ data.uploadDate.replace(/(.{15})..+/, "$1…") }}
              </span>
              <div>
                <span class="like">
                  <button @click="addHeart">
                    <i class="fa fa-heart" aria-hidden="true"></i>
                    <span class="like-counter"> {{ data.total_likes }} </span>
                  </button>
                </span>
              </div>
            </div>
          </li>
        </ul>
      </div>
    </section>
    <!-- Pagination sec  -->
    <PaginateBtn
      :docData="docData"
      :category="categoryText"
      :token="Number(userId)"
      @updateDoc="updateDoc"
    />
  </div>
</template>

<script>
import { onBeforeMount, ref } from "vue";
import { useRoute } from "vue-router";
import ifLiked from "../composable/ifLiked";
import PaginateBtn from "../components/PaginateBtn";
import categoryChanger from "../composable/changeCategory";
import { useStore } from "vuex";
import addOrRemoveHeart from "../composable/addHeart";
export default {
  name: "UserProfile",
  components: { PaginateBtn },
  setup() {
    // Defining variables
    const route = useRoute();
    const store = useStore();
    const userName = ref(null);
    const docData = ref(null);
    const userSkills = ref(null);
    const userBio = ref(null);
    const userProfile = ref(null);
    const token = store.getters.currentUserToken;
    const categoryText = ref(null);
    const host = process.env.VUE_APP_HOST;
    const expendCount = ref(true);
    if (route.params.userId != undefined) {
      store.commit("getUserId", route.params.userId);
    }
    const userId = ref(store.state.userId);
    // Fectching user data
    onBeforeMount(() => {
      fetch(`${host}getuserdocdata`, {
        method: "POST",
        body: JSON.stringify(["null", userId.value]),
        headers: { "Content-type": "application/json; charset=UTF-8" },
      })
        .then((response) => response.json())
        .then((data) => {
          if (data.length > 0) {
            
            categoryText.value = "Your Docs";
            docData.value = data;
            const docIds = [];
            data.forEach((element) => {
              docIds.push(element.id);
            });
            const requestString = `${host}checkiflike/?docIds=${docIds}&token=${store.getters.currentUserToken}`;
            ifLiked(requestString);
          } else {
            categoryText.value = "he or she currently don't have any docs";
          }
        });

      fetch(`${host}getuserprofiledata`, {
        method: "POST",
        body: JSON.stringify(["null", Number(userId.value)]),
        headers: { "Content-type": "application/json; charset=UTF-8" },
      })
        .then((response) => response.json())
        .then((data) => {
          userName.value = data.Username;
          userSkills.value = data.Skills;
          userBio.value = data.Bio;
          userProfile.value = data.ProfileImage;
        });
    });

    //Updating profile data
    const updateDoc = (e) => {
      docData.value = e;

      const docIds = [];
      e.forEach((element) => {
        docIds.push(element.id);
      });
      const requestString = `${host}checkiflike/?docIds=${docIds}&token=${store.getters.currentUserToken}`;
      ifLiked(requestString);
    };

    // ChangeCategory
    const changeCategory = (e) => {
      categoryChanger(
        e,
        docData,
        categoryText,
        Number(store.state.userId),
        store
      );
    };

    // Add heart or like to doc
    const addHeart = (e) => {
      addOrRemoveHeart(e, store);
    };

    // Read details from bio or skill
    const readDetails = (e) => {
      expendCount.value = !expendCount.value;
    };

    // Returning variables
    return {
      route,
      userId,
      token,
      categoryText,
      updateDoc,
      changeCategory,
      docData,
      userName,
      userSkills,
      userBio,
      userProfile,
      expendCount,
      readDetails,
      addHeart,
    };
  },
};
</script>

<style lang="scss">
.user-profile {
  min-height: 90vh;
  margin-bottom: 30px;
  width: 70%;
  margin: auto;
  position: relative;
  .user-details {
    .user {
      display: grid;
      padding-bottom: 20px;
      .user-info {
        display: flex;
        margin-top: 30px;
        width: 100%;
        span {
          padding: 0px 10px;
          i {
            font-size: 10rem;
            color: dodgerblue;
          }
          h1 {
            font-size: 1.2rem;
            text-transform: capitalize;
            font-weight: 400;
            padding: 0px;
            border-radius: 5px;
            color: #222;
            margin: 5px 0px;
          }
        }
        .profile-img {
          width: 200px;
          height: 200px;
          overflow: hidden;
          img {
            border-radius: 4px;
            width: 100%;
            height: 100%;
            object-fit: cover;
            object-position: center;
          }
        }
        p {
          padding: 5px 0px;
          line-height: 25px;
          font-size: 1rem;
          text-transform: capitalize;
          letter-spacing: 1px;
          transition: 0.5 ease;
          width: 60vw;
        }
        .expend {
          transition: 0.5s ease;
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
          width: 60vw;
        }
        b {
          font-size: 1.2rem;
          color: dodgerblue;
          text-transform: capitalize;
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
        }
      }
    }
  }
  .user-docs {
    .user-nav {
      ul {
        list-style: none;
        display: flex;
        justify-content: left;
        padding: 10px 0px;
        li {
          display: inline-block;
          padding: 5px 10px;
          background: dodgerblue;
          text-transform: capitalize;
          margin: 0px 5px;
          color: #fff;
          border-radius: 3px;
          cursor: pointer;
          transition: 0.5s ease;
        }
      }
    }
    .user-doc {
      p {
        font-size: 1.2rem;
        text-transform: none;
        color: #fff;
        font-weight: 900;
        padding: 10px;
        background: dodgerblue;
        border: 0px;
        border-radius: 3px;
      }
      ul {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 20px;
        padding: 20px 0px;
        a {
          text-decoration: none;
          width: 100%;
        }
        li {
          list-style: none;
          background: #222;
          width: 100%;
          margin: auto;
          display: grid;
          grid-template-columns: 3fr 1fr;
          grid-template-rows: auto;
          border-radius: 3px;
          transition: 0.5s ease;
          overflow: hidden;
          &:hover {
            box-shadow: 2px 2px 10px rgb(78, 78, 78);
            transform: scale(1.1);
          }
          .title {
            align-self: center;
            padding: 10px;
            h1 {
              font-size: 1.2rem;
              color: #fff;
              font-weight: 400;
              padding: 0px;
              margin: 0;
              line-height: 35px;
              .doc-tag {
                text-transform: uppercase;
                font-size: 0.8rem;
                background: dodgerblue;
                padding: 5px;
                margin: 5px;
                border-radius: 4px;
              }
            }
          }
          .doc-info {
            color: #fff;
            background: dodgerblue;
            padding: 10px;
            display: flex;
            flex-direction: column;
            span {
              padding: 2px 0px;
              font-size: 0.8rem;
            }
            .like {
              padding: 5px 0px;
              display: flex;
              justify-content: space-between;
              button {
                background: transparent;
                border: 0;
                color: #fff;
                cursor: pointer;

                &:hover > i {
                  transform: scale(1.5);
                  color: red;
                }
                i {
                  pointer-events: none;
                  transition: 0.2s ease-in;
                }
                span {
                  padding: 0px 5px;
                  pointer-events: none;
                }
              }
              .delete-btn {
                &:hover > i {
                  color: orange;
                }
                i {
                  color: fff;
                }
              }
            }
          }
        }
      }
    }
  }
}
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 100;
  background: rgba(0, 0, 0, 0.3);
  display: flex;
  justify-content: center;
  align-items: center;
  .form {
    background: #fff;
    padding: 10px;
    border-radius: 5px;
    .form-title {
      display: flex;
      align-items: center;
      justify-content: space-between;
      h1 {
        font-size: 1.5rem;
        color: #222;
        text-transform: capitalize;
        padding: 5px;
      }
      span {
        padding: 0px 10px;
        cursor: pointer;
        i {
          font-size: 1.22rem;
        }
      }
    }
    .form-control {
      padding: 20px 10px;
      width: 500px;
      label {
        color: #222;
        font-size: 0.9rem;
        text-transform: capitalize;
      }
      input {
        width: 100%;
        margin: 10px 0px;
        border: 0px;
        border-bottom: 1px solid rgb(218, 216, 216);
        text-indent: 10px;
        letter-spacing: 1px;
      }
    }
    .upload-btn-wrapper {
      position: relative;
      overflow: hidden;
      display: inline-block;
      border: 0px;
    }

    .btn {
      color: #fff;
      background-color: #222;
      padding: 5px 10px;
      text-transform: capitalize;
      border: 0px;
      font-size: 1.2rem;
      border-radius: 5px;
    }

    .upload-btn-wrapper input[type="file"] {
      font-size: 100px;
      position: absolute;
      left: 0;
      top: 0;
      opacity: 0;
      border: 0px;
    }
    .submit-btn {
      margin-top: 20px;
      button {
        width: 100%;
        padding: 5px;
        border: 0px;
        background: dodgerblue;
        color: #fff;
        font-size: 1.2rem;
        text-transform: capitalize;
        border-radius: 5px;
        cursor: pointer;
      }
    }
  }
  .delete-warning {
    background: #fff;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: auto;
    padding: 20px;
    border-radius: 4px;
    padding: 20px;
    margin: 10px;
    p {
      font-size: 1rem;
      grid-column: 1 / 3;
      grid-row: 1 / 2;
      padding: 5px;
    }
    button {
      width: 95%;
      margin: 10px auto;
      text-transform: capitalize;
      font-size: 1.3rem;
      background: dodgerblue;
      border: 0px;
      color: #fff;
      padding: 5px;
      cursor: pointer;
    }
  }
}
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.4s linear;
}

.fade-enter,
.fade-leave-to {
  opacity: 0;
}

@media only screen and(max-width:1200px) {
  .user-profile {
    width: 90%;
  }
}
@media only screen and(max-width:880px) {
  .user-profile {
    .user-docs {
      .user-doc {
        ul {
          li {
            grid-template-columns: 1fr 0.7fr;
          }
        }
      }
    }
  }
}
@media only screen and(max-width:650px) {
  .user-profile {
    .user {
      width: 100%;
      padding: 20px 0px;
      .user-info {
        flex-wrap: wrap;
        p {
          width: 80vw;
        }
        .expend {
          width: 80vw;
        }
      }
    }
    .user-doc {
      width: 100%;
      ul {
        li {
          grid-template-columns: 1fr;
          .title {
            h1 {
              text-align: center;
            }
          }
          .doc-info {
            flex-direction: row;
            justify-content: space-evenly;
            span {
              padding: 5px;
            }
          }
        }
      }
    }
  }
  .modal-overlay {
    .form {
      padding: 20px;
      .form-control {
        width: 100%;
      }
    }
  }
}
@media only screen and(max-width:500px) {
  .user-profile {
    .user-docs {
      .user-nav {
        ul {
          justify-content: space-evenly;
          li {
            font-size: 0.8rem;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
          }
        }
      }
      .user-doc {
        ul {
          li {
            grid-template-columns: 1fr;
            .title {
              padding: 20px 10px;
              h1 {
                text-align: left;
              }
            }
            .doc-info {
              flex-direction: column;
              justify-content: space-evenly;
              span {
                padding: 5px;
              }
            }
          }
        }
      }
    }
  }
}
</style>
