const ifLiked = require("../composable/ifLiked");
const { ref } = require("vue");
const host = process.env.VUE_APP_HOST;
const categoryChanger = (e, docData, categoryText, userId, store) => {
    const token = ref(null);
    if (typeof userId == "number") {
        token.value = userId;
    } else {
        token.value = store.getters.currentUserToken;
    }
    const fetchString = ref("");
    if (e.target.getAttribute("data-value") == "latestDoc") {
        fetchString.value = `${host}latestdoc`;
        e.target.nextElementSibling.style.background = "dodgerblue";
        e.target.style.background = "#045db3";
        categoryText.value = "Latest docs";
    } else {
        fetchString.value = `${host}mostlikeddoc`;
        e.target.previousElementSibling.style.background = "dodgerblue";
        e.target.style.background = "#045db3";
        categoryText.value = "Most loved docs";
    }
    fetch(fetchString.value, {
            method: "POST",
            headers: { "Content-type": "application/json; charset=UTF-8" },
            body: JSON.stringify([token.value]),
        })
        .then((response) => response.json())
        .then((data) => {
            if (data.length > 0) {
                docData.value = data;
                const docIds = [];
                data.forEach((element) => {
                    docIds.push(element.id);
                });
                const requestString = `${host}checkiflike/?docIds=${docIds}&token=${store.getters.currentUserToken}`;
                ifLiked(requestString);
            }
        });
};
module.exports = categoryChanger;