<template>
  <div class="doc">
    <!-- Left side  -->
    <div v-if="docData" class="left-side">
      <!-- Doc info  -->
      <section class="doc-info">
        <h1>
          <span>tutorial</span> <br />
          {{ docData.title }}
        </h1>
        <div>
          <span>
            <img
              v-if="docData.profile"
              class="img"
              :src="docData.profile"
              alt=""
            />
            <img
              v-else
              class="img"
              src="https://docbydev.herokuapp.com/assets/uploads/profileImages/image-1612783779868.jpeg"
              alt=""
            />
          </span>
          <span>
            <p>
              by the <b>docbydev</b> community team member
              <b>{{ docData.name }}...</b>
            </p>
            <p>Originally Published on {{ docData.time }}</p>
          </span>
        </div>
      </section>
      <!-- Doc data  -->
      <section class="doc-data" v-html="docData.data"></section>
      <!-- Comment section  -->
      <section class="comments-section">
        <div class="comment-topbar">
          <h1>If you’re facing any problem please Comment below Thanks</h1>
          <ul>
            <li v-if="totalComment">{{ totalComment }} comments</li>
            <li v-else>0 comments</li>
            <li><router-link to="/login">login</router-link></li>
          </ul>
        </div>
        <form id="form">
          <div class="form-control">
            <textarea
              cols="50"
              rows="8"
              placeholder="leave a comment..."
              v-model="comment"
            ></textarea>
            <button @click.prevent="addComment" class="add-comment">
              post
            </button>
          </div>
        </form>
        <div v-if="commentData" class="comment-wraper">
          <div class="comment" v-for="comment in commentData" :key="comment.id">
            <div class="commenter">
              <span v-if="comment.userProfile" class="img">
                <img :src="comment.userProfile" alt="" />
              </span>
              <span v-else class="img">
                <img
                  src="https://docbydev.herokuapp.com/assets/uploads/profileImages/image-1612783779868.jpeg"
                  alt=""
                />
              </span>
              <span class="user-name">
                <b>@{{ comment.userName }}</b>
                <b>{{ comment.uploadDate.replace(/(.{25})..+/, "$1…") }}</b>
              </span>
            </div>
            <p class="expend">
              {{ comment.userComment }}
            </p>
            <span class="read-more" @click="readDetails">read more</span>
          </div>
        </div>
        <div v-else class="no-comment">
          <p>Currently there are no follow-up questions.</p>
        </div>
      </section>
    </div>
    <!-- Right side  -->
    <div class="right-side">
      <!-- About doc sec   -->
      <section class="about-doc">
        <span><h1>more</h1></span>
        <span>by : {{ docData.name }}</span>
        <span>title : {{ docData.title }}</span>
        <span>
          tags :
          <b v-for="(tag, index) in docData.tags.split(',')" :key="index">
            {{ tag }}
          </b>
        </span>
        <span>upload : {{ docData.time }}</span>
      </section>
      <!-- Recomended sec  -->
      <section v-if="recommendedData" class="recommended">
        <ul class="data-ul">
          <h1 class="recommended-title">recommended</h1>
          <li
            class="doc-li"
            v-for="data in recommendedData"
            :key="data.id"
            :data-id="data.id"
          >
            <div
              @click.prevent="newDocData"
              :data-params="
                JSON.stringify({
                  params: {
                    docData: data.docData.slice(1, -1),
                    Username: data.Username,
                    uploadDate: data.uploadDate.replace(/(.{25})..+/, `$1…`),
                    docTitle: data.docTitle,
                    docTags: data.docTags,
                    profileImage: data.ProfileImage,
                    docId: data.id,
                    userId: data.userId,
                  },
                })
              "
              class="title"
            >
              <h1>
                {{ data.docTitle.replace(/(.{35})..+/, "$1…") }}
              </h1>
            </div>
            <div class="doc-info">
              <div>
                <span>
                  <img
                    v-if="data.ProfileImage"
                    :src="data.ProfileImage"
                    alt=""
                  />
                  <img
                    v-else
                    src="https://docbydev.herokuapp.com/assets/uploads/profileImages/image-1612783779868.jpeg"
                    alt=""
                  />
                </span>
                <router-link
                  class="doc-auther"
                  :to="{
                    name: 'UserProfile',
                    params: {
                      userId: data.userId,
                    },
                  }"
                >
                  Auther : {{ data.Username }} <br />
                  Posted : {{ data.uploadDate.replace(/(.{15})..+/, "$1…") }}
                </router-link>
                <span class="like">
                  <button @click="addHeart">
                    <i class="fa fa-heart" aria-hidden="true"></i>
                    <span class="like-counter"> {{ data.total_likes }} </span>
                  </button>
                </span>
              </div>
            </div>
          </li>
        </ul>
      </section>
    </div>
  </div>
</template>

<script>
import { onBeforeMount, onMounted, ref, watch } from "vue";
import { useRoute } from "vue-router";
import { useStore } from "vuex";
import hightlight from "highlight.js";
export default {
  name: "Doc",
  setup() {
    // Defining variables
    const store = useStore();
    const route = useRoute();
    const comment = ref("");
    const host = process.env.VUE_APP_HOST;
    const commentData = ref(null);
    const totalComment = ref(null);
    const recommendedData = ref(null);
    const userProfileData = ref(JSON.parse(localStorage.getItem("userData")));
    if (route.params.docData != undefined) {
      
      store.commit("UpdateDocData", {
        data: route.params.docData,
        time: route.params.uploadDate,
        name: route.params.Username,
        title: route.params.docTitle,
        tags: route.params.docTags,
        profile: route.params.profileImage,
        docId: route.params.docId,
        userId: route.params.userId,
      });
    }
    const docData = ref(store.state.docData);
    

    // Function that add commnent
    const addComment = () => {
      fetch(`${host}addcomment`, {
        method: "POST",
        body: JSON.stringify([
          comment.value,
          store.getters.currentUserToken,
          store.state.docData.docId,
        ]),
        headers: { "Content-type": "application/json; charset=UTF-8" },
      })
        .then((response) => response.json())
        .then((data) => {
          
          if (data.error) {
            alert("You currently not login");
            return;
          } else {
            if (Object.values(data).length > 0) {
              commentData.value = data;
              totalComment.value = Object.values(data).length;
            }
          }
        });
    };

    //Run when component was mounted
    onMounted(() => {
      hightlight.highlightAll();
    });
    //Run before component was mount
    onBeforeMount(() => {
      // Function that gets comments
      fetch(`${host}getcomments`, {
        method: "POST",
        body: JSON.stringify([store.state.docData.docId]),
        headers: { "Content-type": "application/json; charset=UTF-8" },
      })
        .then((response) => response.json())
        .then((data) => {
          if (Object.values(data).length > 0) {
            commentData.value = data;
            totalComment.value = Object.values(data).length;
          }
        });

      fetch(`${host}getdata`)
        .then((response) => response.json())
        .then((data) => {
          const newData = [];
          data.forEach((element) => {
            if (element.docTitle != store.state.docData.title) {
              newData.push(element);
            } else {
              return;
            }
          });
          recommendedData.value = newData;
        });
    });
    // Read details of comments
    const readDetails = (e) => {
      const element = e.target.previousElementSibling;
      if (element.classList.contains("expend")) {
        element.classList.remove("expend");
        e.target.innerHTML = "show less";
      } else {
        element.classList.add("expend");
        e.target.innerHTML = "read more";
      }
    };
    watch(route, () => {
      docData.value = ref(store.state.docData);
    });
    // Returning variables

    const newDocData = (e) => {
      const newDocData = JSON.parse(e.target.getAttribute("data-params"))
        .params;
      
      store.commit("UpdateDocData", {
        data: newDocData.docData,
        time: newDocData.uploadDate,
        name: newDocData.Username,
        title: newDocData.docTitle,
        tags: newDocData.docTags,
        profile: newDocData.profileImage,
        docId: newDocData.docId,
        userId: newDocData.userId,
      });
      docData.value = store.state.docData;
      setTimeout(() => {
        hightlight.highlightAll();
      }, 1000);
      // Function that gets comments
     
      fetch(`${host}getcomments`, {
        method: "POST",
        body: JSON.stringify([newDocData.docId]),
        headers: { "Content-type": "application/json; charset=UTF-8" },
      })
        .then((response) => response.json())
        .then((data) => {
          if (Object.values(data).length > 0) {
            commentData.value = data;
            totalComment.value = Object.values(data).length;
          } else {
            commentData.value = null;
            totalComment.value = Object.values(data).length;
          }
        });
    };
    return {
      docData,
      comment,
      addComment,
      commentData,
      totalComment,
      readDetails,
      userProfileData,
      recommendedData,
      newDocData,
    };
  },
};
</script>

<style lang="scss">
