<template>
  <div class="public-doc">
    <!-- Docs sec  -->
    <div class="docs">
      <nav class="category-btn">
        <h1>public docs <span>explore</span></h1>
        <ul>
          <li @click="changeCategory" data-value="latestDoc">latest docs</li>
          <li @click="changeCategory" data-value="mostLikedDoc">
            most liked docs
          </li>
        </ul>
      </nav>
      <div class="search">
        <div class="input">
          <span><i class="fa fa-search" aria-hidden="true"></i> </span>
          <input
            type="text"
            placeholder="Search"
            @keyup="search"
            v-model="searchDoc"
          />
          <button @click="searchQuery">search</button>
        </div>
      </div>
      <ul class="data-ul">
        <li
          class="doc-li"
          v-for="data in docData"
          :key="data.id"
          :data-id="data.id"
        >
          <router-link
            :to="{
              name: 'Doc',
              params: {
                docData: data.docData.slice(1, -1),
                Username: data.Username,
                uploadDate: data.uploadDate.replace(/(.{25})..+/, '$1…'),
                docTitle: data.docTitle,
                docTags:data.docTags,
                profileImage: data.ProfileImage,
                docId: data.id,
                userId: data.userId,
              },
            }"
            class="title"
          >
            <h1>
              {{ data.docTitle.replace(/(.{35})..+/, "$1…") }}
              <br />
              <span
                v-for="(tag, index) in data.docTags.split(',')"
                :key="index"
                class="doc-tag"
              >
                {{ tag }}
              </span>
            </h1>
          </router-link>
          <div class="doc-info">
            <div>
              <span>
                <img v-if="data.ProfileImage" :src="data.ProfileImage" alt="" />
                <img
                  v-else
                  src="http://localhost:1234/assets/uploads/profileImages/image-1612783779868.jpeg"
                  alt=""
                />
              </span>
              <router-link
                class="doc-auther"
                :to="{
                  name: 'UserProfile',
                  params: {
                    userId: data.userId,
                  },
                }"
              >
                Auther : {{ data.Username }} <br />
                Posted : {{ data.uploadDate.replace(/(.{15})..+/, "$1…") }}
              </router-link>
              <span class="like">
                <button @click="addHeart">
                  <i class="fa fa-heart" aria-hidden="true"></i>
                  <span class="like-counter"> {{ data.total_likes }} </span>
                </button>
              </span>
            </div>
          </div>
        </li>
      </ul>
    </div>
    <!-- Loading sec  -->
    <Loading v-if="!docData" />
    <!-- Loading sec  -->
    <Loading v-if="loading" />
    <!-- Paginate sec  -->
    <PaginateBtn
      :docData="docData"
      :category="categoryText"
      :token="null"
      @updateDoc="updateDoc"
    />
  </div>
</template>
<script>
import { onBeforeMount, ref } from "vue";
import PaginateBtn from "../components/PaginateBtn.vue";
import Loading from "../components/Loading";
import { useStore } from "vuex";
import addOrRemoveHeart from "../composable/addHeart";
import ifLiked from "../composable/ifLiked";
import categoryChanger from "../composable/changeCategory";

// @ is an alias to /src

export default {
  name: "Home",
  components: { PaginateBtn, Loading },
  data() {
    return {
      searchDoc: "",
    };
  },
  methods: {
    search: function() {
      const title = document.querySelectorAll(".title");
      title.forEach((item) => {
        if (
          item.firstElementChild.innerHTML
            .toLowerCase()
            .includes(this.searchDoc.toLowerCase())
        ) {
          item.parentElement.style.display = "grid";
        } else {
          item.parentElement.style.display = "none";
        }
      });
    },
  },
  setup() {
    // Defining variables
    const docData = ref(null);
    const searchDoc = ref("");
    const loading = ref(null);
    const store = useStore();
    const categoryText = ref(null);
    const host = process.env.VUE_APP_HOST;
    // Pagination update doc data
    const updateDoc = (e) => {
      docData.value = e;
      const docIds = [];
      e.forEach((element) => {
        docIds.push(element.id);
      });
      const requestString = `${host}checkiflike/?docIds=${docIds}&token=${store.getters.currentUserToken}`;
      ifLiked(requestString);
    };

    // Get data
    onBeforeMount(async () => {
      fetch(`${host}getdata`)
        .then((response) => response.json())
        .then((data) => {
          
          docData.value = data;
          const docIds = [];
          data.forEach((element) => {
            docIds.push(element.id);
          });
          const requestString = `${host}checkiflike/?docIds=${docIds}&token=${store.getters.currentUserToken}`;
          ifLiked(requestString);
        });
    });

    // Search docs
    const searchQuery = () => {
      loading.value = "loading";

      fetch(`${host}search`, {
        method: "POST",
        body: JSON.stringify([searchDoc.value]),
        headers: { "Content-type": "application/json; charset=UTF-8" },
      })
        .then((response) => response.json())
        .then((data) => {
          loading.value = null;
          docData.value = data.result;
          const docIds = [];
          data.result.forEach((element) => {
            docIds.push(element.id);
          });
          const requestString = `${host}checkiflike/?docIds=${docIds}&token=${store.getters.currentUserToken}`;
          ifLiked(requestString);
        });
    };

    // Like doc
    const addHeart = (e) => {
      addOrRemoveHeart(e, store);
    };

    // Change category
    const changeCategory = (e) => {
      categoryChanger(e, docData, categoryText, "null", store);
    };

    // Returning variables
    return {
      docData,
      updateDoc,
      searchQuery,
      searchDoc,
      loading,
      addHeart,
      changeCategory,
      categoryText,
    };
  },
};
</script>

