import { createStore, Store } from "vuex";

export default createStore({
  state: {
    userToken: null,
    authorizeUser: null,
    docData: JSON.parse(localStorage.getItem("docData")),
    userId: localStorage.getItem("userId"),
  },

  mutations: {
    updateUserToken(state) {
      state.userToken = document.cookie.match(
        "(^|;) ?" + "token" + "=([^;]*)(;|$)"
      );
      if (state.userToken) {
        return (state.userToken = state.userToken[2]);
      } else {
        return (state.userToken = null);
      }
    },
    UpdateDocData(state, payload) {
      state.docData = payload;
      const docData = {
        data: payload.data,
        title: payload.title,
        tags:payload.tags,
        name: payload.name,
        time: payload.time,
        profile: payload.profile,
        docId: payload.docId,
        userId: payload.userId,
      };
      localStorage.setItem("docData", JSON.stringify(docData));
    },
    getUserId(state, payload) {
      state.userId = payload;
      localStorage.setItem("userId", payload);
    },
  },

  actions: {},

  getters: {
    currentUserToken(state) {
      state.userToken = document.cookie.match(
        "(^|;) ?" + "token" + "=([^;]*)(;|$)"
      );
      if (state.userToken) {
        return (state.userToken = state.userToken[2]);
      } else {
        return (state.userToken = null);
      }
    },
  },
  
  modules: {},
});
