const addHeart = (e, store) => {
    const host = process.env.VUE_APP_HOST
    const postId = e.target.parentElement.parentElement.parentElement.parentElement.getAttribute(
        "data-id"
    );
    const docId = e.target.parentElement.parentElement.parentElement.parentElement.getAttribute(
        "data-id"
    );
    const currentNumber = e.target.firstElementChild.nextSibling.textContent;
    fetch(`${host}like`, {
            method: "POST",
            body: JSON.stringify([
                currentNumber,
                store.getters.currentUserToken,
                postId,
                docId,
            ]),
            headers: { "Content-type": "application/json; charset=UTF-8" },
        })
        .then((response) => response.json())
        .then((data) => {

            if (data.message == "Please Login first") {
                alert(data.message);
            } else {
                const likeCounter = document
                    .querySelector(`[data-id='${data.docId}']`)
                    .querySelector(".like-counter");
                // if someone add heart
                if (data.message == "added") {
                    likeCounter.innerHTML = Number(likeCounter.innerHTML) + 1;
                    likeCounter.previousElementSibling.style.color = "red";
                }
                // if someone remove heart
                else if (data.message == "deleted") {
                    likeCounter.innerHTML = Number(likeCounter.innerHTML) - 1;
                    likeCounter.previousElementSibling.style.color = "#fff";
                }
            }
        });
};
module.exports = addHeart;