const ifLiked = (requestString) => {
  fetch(requestString)
    .then((response) => response.json())
    .then((data) => {
      if (data.message) {
        return;
      } else {
        const li = document.querySelectorAll(".doc-li");
        const postId = [];
        data.forEach((element) => {
          postId.push(element.postId);
        });
        li.forEach((element) => {
          const id = Number(element.getAttribute("data-id"));
          if (postId.includes(id)) {
            document
              .querySelector(`[data-id='${id}']`)
              .querySelector(
                ".like"
              ).firstElementChild.firstElementChild.style.color = "red";
          }
        });
      }
    });
};
module.exports = ifLiked;
