<template>
  <div class="add-doc">
    <!-- Doc info  -->
    <div class="info-add__doc">
      <form class="form">
        <div class="form-control">
          <h1>tags</h1>
          <p>
            what tags are you using
            <span>note separate tags with comma(,)</span>
          </p>
          <button class="add-language-btn" @click.prevent="addLanguageBtn">
            html
          </button>
          <button class="add-language-btn" @click.prevent="addLanguageBtn">
            css
          </button>
          <button class="add-language-btn" @click.prevent="addLanguageBtn">
            javascript
          </button>
          <button class="add-language-btn" @click.prevent="addLanguageBtn">
            node.js
          </button>
          <button class="add-language-btn" @click.prevent="addLanguageBtn">
            php
          </button>
          <button class="add-language-btn" @click.prevent="addLanguageBtn">
            python
          </button>
          <button class="add-language-btn" @click.prevent="addLanguageBtn">
            java
          </button>
          <button class="add-language-btn" @click.prevent="addLanguageBtn">
            c++
          </button>
          <button class="add-language-btn" @click.prevent="addLanguageBtn">
            c#
          </button>
          <button class="add-language-btn" @click.prevent="addLanguageBtn">
            c
          </button>
          <p>not what you're looking for?</p>
          <input
            type="text"
            placeholder="Type to add different language"
            required
            v-model="totaltags"
          />
        </div>
        <div class="form-control">
          <h1>doc title</h1>
          <input
            type="text"
            v-model="docTitle"
            placeholder="Add title"
            required
          />
        </div>
      </form>
    </div>
    <!-- Editor -->
    <div class="editor" id="editorjs"></div>
    <button @click.prevent="submitDoc" class="submit-doc">post</button>
  </div>
</template>

<script>
import EditorJS from "@editorjs/editorjs";
import Header from "@editorjs/header";
import List from "@editorjs/list";
const RawTool = require("@editorjs/raw");
const Quote = require("@editorjs/quote");
import ImageTool from "@editorjs/image";
import { onUnmounted, ref } from "vue";
import Embed from "@editorjs/embed";
import { useStore } from "vuex";
import { useRouter } from "vue-router";
export default {
  name: "AddDoc",
  setup() {
    // Defining varables
    const store = useStore();
    const router = useRouter();
   
    const host = process.env.VUE_APP_HOST;
    const totaltags = ref("");
    const docTitle = ref("");

    // EditorJS config
    const editor = new EditorJS({
      holder: "editorjs",
      tools: {
        header: {
          class: Header,
          inlineToolbar: ["link"],
        },
        list: {
          class: List,
          inlineToolbar: true,
        },
        embed: {
          class: Embed,
          config: {
            services: {
              youtube: true,
              coub: true,
            },
          },
        },
        raw: {
          class: RawTool,
        },
        quote: Quote,
        image: {
          class: ImageTool,
          config: {
            endpoints: {
              byFile: `${host}uploadFile`, // Your backend file uploader endpoint
              byUrl: `${host}fetchUrl`, // Your endpoint that provides uploading by Url
            },
          },
        },
      },
    });
    // SubmitDoc handler
    const submitDoc = () => {
      if (docTitle.value != "" && totaltags.value != "") {
        totaltags.value = totaltags.value.split(",");
        const uniqueTotalTags = totaltags.value.filter((value, index, self) => {
          return self.indexOf(value) === index;
        });
        if (uniqueTotalTags.includes("")) {
          uniqueTotalTags.pop();
        }
        totaltags.value = "";
        editor.save().then((outputData) => {
          const docData = [];
          outputData.blocks.forEach((item) => {
            if (item.type == "paragraph") {
              item.data.text = item.data.text.replace(/&nbsp;/g, "");
              item.data.text = item.data.text.replace(/'/g, "`");
              item.data.text = item.data.text.trim();
              item.data.text = item.data.text.replace(/&lt;br&gt;/g, "");
              docData.push(`<p>${item.data.text}</p>`);
            }
            // Checking if item type is Paragraph
            if (item.type == "raw") {
              item.data.html = item.data.html.replace(/&nbsp;/g, "");
              item.data.html = item.data.html.replace(/'/g, "`");
              item.data.html = item.data.html.trim();
              item.data.html = item.data.html.replace(/&lt;br&gt;/g, "");
              item.data.html = item.data.html.replace(/</g, "&lt;");
              item.data.html = item.data.html.replace(/>/g, "&gt;");
              docData.push(`<pre><code>${item.data.html}</code></pre>`);
            }
            // Checking if item type is Image
            if (item.type == "image") {
              item.data.caption = item.data.caption.replace(/&nbsp;/g, "");
              item.data.caption = item.data.caption.trim();
              item.data.caption = item.data.caption.replace(/&lt;br&gt;/g, "");
              item.data.caption = item.data.caption.replace(/'/g, "`");
              docData.push(`<img src="${item.data.file.url}">`);
              docData.push(`<caption>${item.data.caption}</caption>`);
            }
            // Checking if item type is Embed
            if (item.type == "embed") {
              item.data.caption = item.data.caption.replace(/&nbsp;/g, "");
              item.data.caption = item.data.caption.replace(/'/g, "`");
              item.data.caption = item.data.caption.trim();
              item.data.caption = item.data.caption.replace(/&lt;br&gt;/g, "");
              docData.push(`<iframe src="${item.data.embed}"></iframe>`);
              docData.push(`<caption>${item.data.caption}</caption>`);
            }
            // Checking if item type is Header
            if (item.type == "header") {
              item.data.text = item.data.text.replace(/&nbsp;/g, "");
              item.data.text = item.data.text.trim();
              item.data.text = item.data.text.replace(/'/g, "`");
              item.data.text = item.data.text.replace(/&lt;br&gt;/g, "");
              docData.push(`<h1>${item.data.text}</h1>`);
            }
            // Checking if item type is List
            if (item.type == "list") {
              let list = document.createElement("div");
              let ul = document.createElement("ul");
              item.data.items.forEach((i) => {
                let li = document.createElement("li");
                i = i.replace(/&nbsp;/g, "");
                i = i.replace(/'/g, "");
                i = i.trim();
                i = i.replace(/&lt;br&gt;/g, "");
                i = i.replace(/'/g, "");
                li.innerHTML = i;
                ul.append(li);
              });
              list.append(ul);
              docData.push(list.innerHTML);
            }
          });
          fetch(`${host}adddoc`, {
            method: "POST",
            body: JSON.stringify([
              docData,
              docTitle.value,
              uniqueTotalTags,
              store.getters.currentUserToken,
            ]),
            headers: { "Content-type": "application/json; charset=UTF-8" },
          })
            .then((response) => response.json())
            .then((data) => {
              alert(data.message);
              router.go({ name: "Home" });
            });
        });
      } else {
        alert("Please Enter Your Doc Title And Atleast One Langauge");
      }
    };

    const addLanguageBtn = (e) => {
      if (
        totaltags.value == "" &&
        totaltags.value.includes(`${e.target.innerHTML.trim()}`) === false
      ) {
        totaltags.value += `${e.target.innerHTML.trim()},`;
      } else if (
        totaltags.value.includes(`${e.target.innerHTML.trim()},`) === false
      ) {
        totaltags.value += `${e.target.innerHTML.trim()},`;
      }
    };
    // Destroying enditor
    onUnmounted(() => {
      editor.destroy();
    });
    return { submitDoc, addLanguageBtn, totaltags, docTitle };
  },
};
</script>

<style lang="scss">
.add-doc {
  box-shadow: 1px 1px 10px rgb(201, 201, 201);
  border: 0px;
  border-radius: 5px;
  width: 80%;
  margin: 50px auto;
  overflow: hidden;
  .info-add__doc {
    padding: 10px;
    background: #222;
    width: 100%;
    .form {
      .form-control {
        h1 {
          color: #fff;
          text-transform: capitalize;
          font-size: 2rem;
          font-weight: 400;
          padding: 10px;
        }
        p {
          color: #fff;
          font-size: 1.2rem;
          font-weight: 400;
          text-transform: capitalize;
          padding: 10px;
          span {
            display: table;
            font-size: 0.9rem;
            padding: 5px;
            border-radius: 3px;
            background: yellow;
            color: #222;
            margin: 10px 0px;
          }
        }
        .add-language-btn {
          padding: 5px 20px;
          text-transform: uppercase;
          font-size: 1rem;
          margin: 10px;
          background: dodgerblue;
          border: 0px;
          color: #fff;
          border-radius: 3px;
          cursor: pointer;
          transition: 0.5s ease-in-out;
          &:hover {
            background: #fff;
            color: #222;
          }
        }
        input {
          width: 100%;
          padding: 10px;
          text-indent: 10px;
          border-radius: 3px;
          border: 1px solid #2222;
          margin: 5px 0px;
        }
      }
    }
  }
  .editor {
    min-height: 60vh;
  }
  .submit-doc {
    width: 100%;
    border: 0px;
    padding: 10px;
    text-transform: capitalize;
    font-size: 1.3rem;
    background: #222;
    color: #fff;
    cursor: pointer;
    transition: 0.5s ease-in-out;
    &:hover {
      background: dodgerblue;
    }
  }
}
@media only screen and(max-width:700px) {
  .add-doc {
    width: 95%;
  }
}
@media only screen and(max-width:700px) {
  .add-doc {
    .info-add__doc {
      h1 {
        font-size: 1.5rem;
      }
    }
  }
}
</style>
